import React, { Component } from 'react';
import Nav from '../component/Nav';
import ReactDom from 'react-dom';
import Alert from '../component/Alert';
export default class Signup extends Component {
    constructor(){
        super();
        this.state = {
            msg:'',
            btn:'',
            fun:()=>{},
            src:''
        }
    }
    render() {
        return (
            <div className='login_bg'>
                <Nav title=''/>
                <div className='login_logo animated fadeInDown'>
                    <img src='/images/logo.png' width='100px' alt=''/>
                </div>
                <Alert msg={this.state.msg} toPath={()=>{this.state.fun()}} btn={this.state.btn} src={this.state.src}/>
                <input type='email' placeholder='      name@qq.com' className='login_input animated fadeInDown' name='email' autoComplete='off'/>
                <input type='text' placeholder='' className='login_input animated fadeInDown' name='username' autoComplete='off'/>
               <input type='password' placeholder='' className='login_input animated fadeInDown' id='password' name='password' onKeyUp={(e)=>{this.check(e)}}/>
               <input type='password' placeholder='' className='login_input animated fadeInDown' onKeyUp={(e)=>{this.check(e)}} id='password_check'/>
                <span id='login_check' className='animated shake'>{''}</span>
               <input type='button' value='' className='input_btn animated fadeInDown' id='logup_btn' onClick={(e)=>{this.fetch_logup(e)}}/>
               <input type='button' value='' className='input_btn animated fadeInDown' onClick={(e)=>{this.toLogin(e)}}/>
            </div>
        )
    }
    toLogin = (e)=>{
        this.props.history.push('/');
    }
    check = (e)=>{
        let value =  ReactDom.findDOMNode(document.getElementById('password_check')).value;
        let value1 = ReactDom.findDOMNode(document.getElementById('password')).value;
        if(value === value1){
            ReactDom.findDOMNode(document.getElementById('login_check')).style.display='none';
            ReactDom.findDOMNode(document.getElementById('logup_btn')).disable=true;
        }else{
            ReactDom.findDOMNode(document.getElementById('login_check')).style.display='block';
            ReactDom.findDOMNode(document.getElementById('login_check')).disable=false;
        }
    }
    fetch_logup=(e)=>{
        let data = {};
        data.type='logup';
        data.email = document.getElementsByName('email')[0].value;
        data.username = document.getElementsByName('username')[0].value;
        data.password = document.getElementsByName('password')[0].value;
        if(data.email === ''||data.username===''||data.password===''){
            this.setState({
                msg:'',
                btn:'',
                fun:()=>{
                    ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='none';
                }
            },()=>{
                ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='block';
            })
        }else{
            fetch('https://daitianfang.1459.top/acg',{
                method:'POST',
                headers:{
                    'Content-Type':'application/json'
                },
                mode:'cors',
                body:JSON.stringify(data)
            }).then(req=>req.text()).then(text=>{
                if(text === 'success'){
                    this.setState({
                        msg:',',
                        btn:'',
                        fun:()=>{
                            sessionStorage.setItem('username',data.username);
                            sessionStorage.setItem('email',data.email);
                            this.props.history.push('/email');
                        },
                        src:'/images/success.png'
                    },()=>{
                        ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='block';
                    })
                }else{
                    this.setState({
                        msg:text,
                        btn:'',
                        fun:()=>{
                            ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='none';
                        }
                    },()=>{
                        ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='block';
                    })
                }
            })
        }

    }
}
