import React, { Component } from 'react';
import Nav from '../component/Nav';
import Alert from '../component/Alert';
import ReactDom from 'react-dom';
export default class Activate extends Component {
    constructor(){
        super();
        this.state = {
            msg:'',
            btn:'',
            fun:()=>{},
            src:''
        }
    }
    render() {
        return (
            <div className='login_bg'>
                <Nav title=''/>
                <Alert msg={this.state.msg} btn = {this.state.btn} toPath={()=>{this.state.fun()}} src={this.state.src}/>
                <button className='input_btn animated zoomIn' onClick={()=>{this.fetch_send()}}></button>
            </div>
        )
    }
    fetch_send = ()=>{
        let data = {};
        data.type = 'check';
        data.username = sessionStorage.getItem('username');
        data.email = sessionStorage.getItem('email');
        ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='block';
        this.setState({
            msg:'   ',
            btn:'',
            fun:()=>{
                ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='none';
            },
            src:'/images/run.gif'
        })
        fetch('https://daitianfang.1459.top/acg',{
            method:'POST',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify(data)
        }).then(req=>req.text()).then(data=>{
            if(data === 'success'){
                this.setState({
                    msg:'',
                    btn:'',
                    fun:()=>{
                        this.props.history.push('/');
                    },
                    src:'/images/success.png'
                },()=>{
                    ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='block';
                })
            }else if(data === ','){
                this.setState({
                    msg:data,
                    btn:'',
                    fun:()=>{
                        this.props.history.push('/logup');
                    },
                    src:'/images/failed.png'
                },()=>{
                    ReactDom.findDOMNode(document.getElementById('login_alert')).style.display='block';
                })
            }
        })
    }
}
