import React, { Component } from 'react'
import {Link} from 'react-router-dom'
export default class Leftmenu extends Component {
    constructor(){
        super();
        this.state = {
            data: [],
            cookie_obj:this.cookieToObj(document.cookie)
            
        }
    }
    cookieToObj=(cookie)=>{
        let obj = {};
        if(cookie){
            cookie.split(';').map(item=>{
                item = item.trim();
                let arr = item.split('=');
                obj[arr[0]] = arr[1];
            });
        }
        return obj;
        
      }
    render() {
        if(this.state.data.length===0){
            return(
                <div className='menu0'>

                
                    <div>
                       <div className='menu1'>
                            <img src='/img/Logo.png' style={{width:'70px',height:'70px',borderRadius:'50%'}} alt=''/>
                            <p></p>
                            <p style={{color:'orange',fontSize:'12px'}}></p>
                         </div> 
                    </div>
                     <div className='menu1'>
                     <ul className='menu2'>
                     <Link to='/apphome'></Link>
                     </ul>
                     <ul className='menu2'>
                     <Link to='/person'></Link>
                     </ul>
                     <ul className='menu2'>
                     <Link to='/appgood'></Link>
                     </ul>
                     <ul className='menu2'>
                     <Link to='/person'></Link>
                     </ul>
                     <ul className='menu2'>
                     <Link to='/person'></Link>
                     </ul>
                     <ul className='menu2'>
                     <Link to='/aboutus'></Link>
                     </ul>
                    
                 </div>
                 <div>
                 <Link to = '/setting'><img src='/img/setting.png' className='menu3'  alt=''/></Link>
                 
                </div>
                </div>
            )
        }
        else{
            return (
                <div className='menu0'>
                   <div>
                       
                  
                    {this.state.data.map((item,key)=>(
                        <div key={key}>
                           <div className='menu1'>
                                <img src={"/images/avatar/"+item.avatarid } style={{width:'70px',height:'70px',borderRadius:'50%'}} alt=''/>
                                <p style={{color:'black',fontSize:'17px'}}>{item.name}</p>
                                <p style={{color:'black',fontSize:'16px'}}>{item.signatrue}</p>
                             </div>
                             <div className='menu1'>
                            <ul className='menu2'>
                            <Link to='/apphome'></Link>
                            </ul>
                            <ul className='menu2'>
                            <Link to='/appgood'></Link>
                            </ul>
                            <ul className='menu2'>
                            <Link to='/person'></Link>
                            </ul>
                            <ul className='menu2'>
                            <Link to={'/concern/'+item.id}></Link>
                            </ul>
                            <ul className='menu2'>
                            <Link to={'/fans/'+item.id}></Link>
                            </ul>
                            <ul className='menu2'>
                            <Link to='/aboutus'></Link>
                            </ul>
                            
                        </div> 
                        </div>
                            
                    ))
                    } 
                    
                        
    
                    </div>
                    <div>
                        <Link to = '/setting'><img src='/img/setting.png' className='menu3'  alt=''/></Link>
                        
                    </div>
                    </div>
            )  
        }
        
    }
    componentDidMount(){
        fetch('https://daitianfang.1459.top/api/v1/person?id='+this.state.cookie_obj.userid)
        .then((res)=>res.json())
        .then((res)=>{          
            this.setState({data:res.data});
        })
    }
}